.wrap {
  overflow: hidden;
}
.btn-red {
  background: #FF2B2B;
  border: 1px solid #FF2B2B;
  span {
    font-size: 16px;
    line-height: 120%;
    font-weight: 700;
    text-transform: uppercase;
    color: #F7F8FA;
  }
  &:hover {
    background: #FF5757;
  }
}
.text36 {
  font-size: 36px;
  line-height: 140%;
  font-weight: 800;
  color: #4E4D4D;
}
.text20 {
  font-size: 20px;
  line-height: 140%;
  color: #A6A6A6;
}
.navigation {
  padding: 50px 15px;
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    li {
      font-size: 24px;
      line-height: 120%;
      a:hover {
        color: #ff5757;
      }
    }
  }
}
.form {
  &-grid {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
  }
  &-group {
    border: none;
    padding: 0;
    margin: 0;
    .input-title {
      display: block;
      font-size: 20px;
      line-height: 120%;
      color: #000000;
      margin-bottom: 5px;
    }
    input {
      width: 100%;
      height: 50px;
      border: 1px solid #9B9B9B;
      background: #FFF;
      font-size: 20px;
      line-height: 120%;
      color: #000000;
      padding: 5px 15px;
      &::placeholder {
        color: #9B9B9B;
      }
      &:focus {
        box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.15) inset;
      }
    }
    textarea {
      width: 100%;
      height: 88px;
      border: 1px solid #9B9B9B;
      background: #FFF;
      font-size: 20px;
      line-height: 120%;
      color: #000000;
      padding: 15px;
      resize: none;
      &::placeholder {
        color: #9B9B9B;
      }
      &:focus {
        box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.15) inset;
      }
    }
  }
  &-checkbox {
    display: inline-flex;
    position: relative;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      cursor: pointer;
      opacity: 0;
      &:checked ~ label {
        &:before {
          opacity: 1;
          pointer-events: all;
        }
      }
    }
    label {
      font-size: 16px;
      line-height: 120%;
      color: #9B9B9B;
      padding-left: 30px;
      position: relative;
      a {
        position: relative;
        z-index: 3;
        text-decoration: underline;
        text-underline-offset: 3px;
      }
      a:hover {
        color: #ff5757;
      }
      &:after {
        content: '';
        position: absolute;
        top: -1px;
        left: 0;
        width: 20px;
        height: 20px;
        background: #FFF;
        border: 1px solid #9B9B9B;
      }
      &:before {
        content: '';
        position: absolute;
        z-index: 1;
        top: 5px;
        left: 5px;
        width: 12px;
        height: 8px;
        background-image: url("../img/check-icon.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
        opacity: 0;
        pointer-events: none;
        transition: all 0.5s;
      }
    }
  }
  &-flex {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    input {
      width: 178px;
      margin-right: 15px;
      margin-bottom: 10px;
    }
    img {
      width: 210px;
      margin-bottom: 10px;
      img {
        width: 100%;
      }
    }
  }
  &-btn {
    width: 100%;
    height: 58px;
  }
  &-info {
    font-size: 12px;
    line-height: 120%;
    color: #888;
    a {
      text-decoration: underline;
      text-underline-offset: 3px;
    }
    a:hover {
      color: #ff5757;
    }
  }
}
.modal {
  background: rgba(39, 62, 81, 0.85);
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  overflow-y: auto;
  z-index: 1000;
  opacity: 0;
  pointer-events: none;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  @media screen and (min-width: 993px) {
    text-align: center;
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%;
    }
    &__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
  &__dialog{
    width: 750px;
    max-width: 100%;
    position: relative;
    border-radius: 20px;
    background: #FFF;
    box-shadow: 0px 4px 20px 0px rgba(0, 0, 0, 0.55);
    margin: 0 auto;
    @media screen and (max-width: 992px) {
      margin: 50px auto;
      max-width: calc(100% - 30px);
    }
  }
  &__close {
    position: absolute;
    top: -24px;
    right: 0;
    font-size: 12px;
    line-height: 120%;
    font-weight: 700;
    color: #ffffff;
    background: transparent;
    padding: 0;
    margin: 0;
  }
  &__content {
    display: flex;
    align-items: stretch;
  }
  &__left {
    width: 50%;
    padding: 30px;
  }
  &__right {
    width: 50%;
    position: relative;
  }
  &__title {
    font-size: 24px;
    line-height: 120%;
    font-weight: 800;
    color: #000000;
    text-align: center;
  }
  &__form {
    margin-top: 20px;
    &-group {
      input {
        border: 1px solid #D9D9D9;
        background: #F7F8FA;
      }
      textarea {
        border: 1px solid #D9D9D9;
        background: #F7F8FA;
      }
    }
  }
  &__img {
    height: 100%;
    img {
      width: 100%;
      height: 100%;
      border-radius: 0 20px 20px 0;
    }
  }
  &__info {
    position: absolute;
    left: 50%;
    bottom: 25px;
    transform: translate(-50%, 0);
    width: 100%;
    text-align: center;
    padding: 0 15px;
    &-icon {
      width: 37px;
      margin: 0 auto;
      img {
        width: 100%;
      }
    }
    &-text {
      font-size: 16px;
      line-height: 120%;
      font-weight: 300;
      color: #FFF;
      text-align: center;
      text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
      margin-top: 9px;
    }
  }
}


@media screen and (max-width: 992px) {
  .text36 {
    font-size: 24px;
  }
  .text20 {
    font-size: 16px;
  }
  .modal {
    &__dialog {
      margin-bottom: 130px;
    }
    &__content {
      flex-direction: column;
    }
    &__left {
      width: 100%;
    }
    &__right {
      width: 100%;
      order: -1;
      height: 350px;
      position: static;
    }
    &__img {
      img {
        border-radius: 20px 20px 0 0;
      }
    }
    &__info {
      bottom: -100px;
    }
  }
}

@media screen and (max-width: 576px) {
  .modal {
    &__left {
      padding: 20px 15px;
    }
    &__right {
      height: 180px;
    }
  }
}