.contacts {
  padding: 50px 0;
  &__content {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    margin-top: 30px;
  }
  &__left {
    width: 615px;
    margin-right: 40px;
  }
  &__right {
    width: calc(100% - 655px);
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
  }
  &__item {
    font-size: 20px;
    line-height: 120%;
    span {
      color: #9B9B9B;
    }
    a {
      color: #000;
    }
    a:hover {
      color: #ff5757;
    }
  }
  &__form {
    margin-top: 40px;
    &-grid {
      margin-top: 15px;
    }
  }
  .map{
    width: 100%;
    height: 100%;
    .ymaps-2-1-79-map {
      width: 100% !important;
      height: 100% !important;
    }
  }
}

@media screen and (max-width: 1200px) {
  .contacts {
    &__left {
      width: 450px;
      margin-right: 30px;
    }
    &__right {
      width: calc(100% - 480px);
    }
  }
}

@media screen and (max-width: 992px) {
  .contacts {
    padding: 15px 0 40px;
    &__content {
      display: block;
    }
    &__left {
      width: 100%;
      margin: 0 0 30px 0;
    }
    &__right {
      width: 100%;
      height: 500px;
    }
  }
}