.about {
  padding: 50px 25px;
  &__text {
    margin-top: 40px;
    p {
      font-size: 20px;
      line-height: 200%;
      color: #4E4D4D;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 30px / 2));
    gap: 30px;
    margin-top: 20px;
  }
  &__item {
    text-align: center;
    &-img {
      img {
        width: 100%;
      }
    }
    &-text {
      font-size: 16px;
      line-height: 120%;
      color: #A6A6A6;
      margin-top: 10px;
    }
  }
}
.about-services {
  padding: 25px 0;
  &__title {
    font-size: 32px;
    line-height: 120%;
    color: #4E4D4D;
  }
  &__subtitle {
    font-size: 20px;
    line-height: 120%;
    font-weight: 700;
    color: #4E4D4D;
    margin-top: 30px;
  }
  &__list {
    margin-top: 30px;
    li {
      list-style: disc;
      list-style: inside;
      font-size: 20px;
      line-height: 200%;
      color: #4E4D4D;
      &:not(:first-child) {
        margin-top: 10px;
      }
    }
  }
}
.about-advantages {
  padding: 25px 0 50px;
  &__list {
    li {
      list-style: auto;
      list-style-position: inside;
    }
  }
  &__select {
    width: 318px;
    height: 100%;
    min-height: 34px;
    margin-top: 30px;
  }
  .nice-select {
    width: 100%;
    height: 100%;
    min-height: 34px;
    display: flex;
    align-items: center;
    background: #F7F8FA !important;
    border: 1px solid #D9D9D9;
    padding: 3px 12px;
    .current {
      font-size: 16px;
      line-height: 120%;
      color: #000000;
    }
    .list {
      border: 1px solid #D9D9D9;
      background: #F7F8FA;
      box-shadow: 2px 4px 4px 0px rgba(0, 0, 0, 0.10);
      li {
        display: flex;
        align-items: center;
        height: 100%;
        min-height: 34px;
        font-size: 16px;
        line-height: 120%;
        color: #000000;
        padding: 3px 12px;
      }
    }
    .option.focus, .nice-select .option.selected.focus {
      background: rgba(49, 69, 92, 0.20);
    }
  }
  &__table {
    margin-top: 20px;
    overflow-x: auto;
    padding-bottom: 10px;
    &::-webkit-scrollbar {
      height: 3px;
    }
    &::-webkit-scrollbar-track {
      background: #ffffff;
      border-radius: 3px;
    }
    &::-webkit-scrollbar-thumb {
      background: #000000;
      border-radius: 3px;
    }
    table {
      width: 1410px;
      border-collapse: collapse;
      thead {
        tr {
          background: #273E51;
        }
        th {
          text-align: start;
          font-size: 20px;
          line-height: 120%;
          color: #ffffff;
          padding: 12px;
        }
      }
      tbody {
        tr:nth-child(even) {
          background: #ffffff;
        }
        tr:nth-child(odd) {
          background: rgba(39, 62, 81, 0.10);
        }
        td {
          font-size: 20px;
          line-height: 120%;
          color: #000000;
          padding: 12px;
        }
        td:first-child {
          font-weight: 700;
        }
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .about {
    padding: 25px 0 20px;
    &__text {
      margin-top: 25px;
      p {
        font-size: 16px;
      }
    }
  }
  .about-services {
    padding: 20px 0;
    &__title {
      font-size: 24px;
    }
    &__subtitle {
      font-size: 16px;
    }
    &__list {
      li {
        font-size: 16px;
      }
    }
  }
  .about-advantages {
    padding: 20px 0 40px;
  }
}

@media screen and (max-width: 768px) {
  .about {
    &__items {
      grid-template-columns: 100%;
    }
  }
}

@media screen and (max-width: 576px) {
  .about-advantages {
    &__select {
      width: 100%;
    }
  }
}